<template>
  <div id="app">
    <v-container>
      <p class="display-1 pink--text text-xs-left" style="  font-family: 'Montserrat', sans-serif !important;"><b>GOT MEMORY GAME</b></p>
      <p class="title">SCORE: {{score}}</p>
      <v-layout justify-center>
        <v-flex xs10>
          <v-layout row wrap>
            <v-flex xs3 sm3 v-for="(card,ind) in cards" :key="ind">
              <v-card class="ma-2" height="175" style="box-shadow: 5px 7px pink" raised>
                <v-container @click=flip(ind) class="pa-0">
                  <v-card-media
                    v-if="card.state || card.solved"
                    height="175"
                    :src="card.src"
                  ></v-card-media>
                <!-- <v-card
                  height="175"
                  v-else-if="card.solved"
                ></v-card> -->
                  <v-card-media
                    v-else
                    height="175"
                    src="https://wallpaper-gallery.net/images/game-of-thrones-minimalist-wallpaper/game-of-thrones-minimalist-wallpaper-8.jpg"
                  >
                  </v-card-media>
                </v-container>
              </v-card>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
      <v-dialog v-model="dialog" width="500" height="200" class="text-xs-center justify-center">
        <v-card color="white">

          <p class="pt-3 display-1 green--text align-center text-xs-center"   style="font-family: 'Montserrat', sans-serif !important;">
            <b>You win</b>
          </p>
          <p class=" mb-2 subheading green--text align-center text-xs-center"   style="font-family: 'Montserrat', sans-serif !important;">
            <b>Your score : <span class="pink--text">{{score}}</span></b>
          </p>
          <v-layout justify-center pb-3>
            <v-btn
              style="border:0.5px solid pink"
              round
              class="pink--text"
              @click="again"
            >
              PLAY AGAIN
            </v-btn>
          </v-layout>
      </v-card>
      </v-dialog>
    </v-container>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      dialog: false,
      cards: [
        {
          src: require('./assets/arya.png'),
          state: false,
          solved: false
        },
        {
          src: require('./assets/cersei.png'),
          state: false,
          solved: false
        },
        {
          src: require('./assets/dany.png'),
          state: false,
          solved: false
        },
        {
          src: require('./assets/jaimie.png'),
          state: false,
          solved: false
        },
        {
          src: require('./assets/jon.png'),
          state: false,
          solved: false
        },
        {
          src: require('./assets/tyrion.png'),
          state: false,
          solved: false
        }
      ],
      shuffled: [],
      score: 0
    }
  },
  methods: {
    shuffle (original) {
      let ctr = original.length
      let temp
      let index
      // While there are elements in the array
      while (ctr > 0) {
        // Pick a random index
        index = Math.floor(Math.random() * ctr)
        // Decrease ctr by 1
        ctr--
        // And swap the last element with it
        temp = original[ctr]
        original[ctr] = original[index]
        original[index] = temp
      }
    },
    flip (ind) {
      var opened = this.cards.filter((a) =>
        a.state === true
      )

      if (opened.length < 2) {
        this.cards[ind].state = !this.cards[ind].state
        this.score++
        console.log(this.score)
        this.counter(ind)
      }
    },

    counter (ind) {
      var that = this
      var opened = this.cards.filter((a) =>
        a.state === true
      )

      if (opened.length === 1) {
        setTimeout(function () {
          that.cards[ind].state = false
        }, 1000)
      }

      if (opened.length === 2) {
        // success
        if (opened[0].src === opened[1].src) {
          that.cards.find(function (item) {
            if (item.src === opened[0].src) {
              var ind = that.cards.indexOf(item)
              that.cards[ind].solved = true
              if (that.score > 2) {
                that.score -= 2
              }
            }
          })
        }

        // closing back
        this.cards.forEach(function (a) {
          setTimeout(function () {
            a.state = false
          }, 1000)
        })

        // you win
        if (this.cards.every((e) => e.solved === true)) {
          console.log('jeet')
          
          this.dialog = true
          that.score -= 2
        }
      }
    },
    
    again () {
      this.dialog = false
      this.score = 0
      this.cards.forEach(element => {
        element.solved = false
        element.state = false
      })
    }
  },
  created () {
    this.cards = this.cards.concat(JSON.parse(JSON.stringify(this.cards)))
    this.shuffle(this.cards)
  }
}
</script>

